.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;

}
.header-bg{
    background-color: rgb(0,0,0,0.85);
    transition: 0.5s;



}
.navmenu{
    display: flex;
}
.navmenu li {
    padding: 1rem;
}

.navmenu li a {
    font-size: 1.2rem;
    font-weight: 500;


}

.hamburger{
    display: none;


}
@media screen and (max-width: 1040px) {
    .navmenu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.9);
        position: absolute;
        top: 0;
        left:-100%;
        z-index: -3;
        transition: 0.3s;


    }
    .navmenu.active{
        left: 0;
}

    .navmenu li{
        padding: 1rem 0;
}

.navmenu li a{
    font-size: 1.7rem;


}
.hamburger{
    display:initial;


}
    
}

